.profile {
	padding: 2.625em 0 0;
	max-width: calc(100% - 2.375em);
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	margin: 0 auto;
}

@media screen and (min-width: 768px) {
	.profile {
		flex-direction: row;
	}
}

@media screen and (min-width: 880px) {
	.profile {
		max-width: 100%;
	}
}
